  body {
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, pink 0%, pink 50%, cyan 50%, cyan 100%);
  }
  
  .about {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    font-size: 1.5rem;
    padding: 2rem;
    font-family: sans-serif;
    background-color: rgb(234, 239, 245);
    margin: 2rem auto;
    box-shadow: 15px 15px 20px #6e6969;
    border-radius: 0.8rem;
    font-weight: bold;
  }
  
  .developers {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    font-size: 1.5rem;
    padding: 3rem;
    font-family: sans-serif;
    background-color: rgb(234, 239, 245);
    margin: 3.5% auto;
    box-shadow: 15px 15px 20px #6e6969;
    border-radius: 0.8rem;
    font-weight: bold;
  }
  h4 {
    padding: 0.6rem 2rem 0.6rem 2rem;
  }
  .scoreboard {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 23rem;
    font-size: 2.5rem;
    background-color: rgb(243, 239, 239);
    margin: 2rem auto 4rem;
    box-shadow: 15px 15px 20px #6e6969;
    border-radius: 0.8rem;
    font-weight: bold;
  }
  
  body {
    margin: 0;
    background-color: #5feed6;
    font-family: "Fredoka", sans-serif;
  }
  